body {
  background-color: #202020;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

@media (max-width: 767px) {
  .py-8 {
    padding-top: 40px !important;
    padding-bottom: 40px !important;
  }
}

.navbar::after, .navbar::before {
  content: "";
  width: 50%;
  height: 2px;
  position: absolute;
  bottom: 0;
}

.navbar::after {
  background-color: #62DB54;
}

.navbar::before {
  background-color: #09ACF5;
  right: 0;
}

.navbar-brand {
  height: 42px;
  width: 170px;
  background: url("../images/logo.svg") no-repeat center;
}

@media (max-width: 767px) {
  .navbar-brand {
    background: url("../images/logo2.svg") no-repeat left;
  }
}

.navbar-toggler {
  border: none;
}

.navbar-toggler:focus {
  box-shadow: none;
}

@media (max-width: 992px) {
  .navbar .navbar-nav {
    align-items: center;
  }
}

.navbar .nav-link:hover {
  color: #62DB54 !important;
}

.navbar .modal-body input {
  min-width: 330px;
}

.navbar .modal-body .user-btn {
  background-image: linear-gradient(to left, #62DB54, #09ACF5);
  border-radius: 8px;
  transition: all .2s ease-in;
}

.navbar .modal-body .user-btn:hover {
  transform: scale(1.05);
}

.navbar .modal-body img {
  left: 20%;
}

.navbar .btn-close {
