    line-height: 14px;
  }
}

.banner {
  background: url("../images/bg_home.jpg") no-repeat center;
  background-size: cover;
  height: calc(100vh - 90px);
}

@media (max-width: 1400px) {
  .banner {
    height: 680px;
  }
}

@media (max-width: 767px) {
  .banner {
    background-image: url("../images/bg_home_mobile.jpg");
    height: 500px;
  }
}

