    height: 336px;
  }
}

.banner h2 {
  font-size: 3rem;
  line-height: 70px;
  font-weight: bold;
}

@media (max-width: 767px) {
  .banner h2 {
    font-size: 1.5rem;
    line-height: 35px;
    font-weight: bold;
  }
}

.banner h2::after {
  content: "";
  width: 10px;
  height: 10px;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
}

.banner h2::before {
  content: "";
  width: 10px;
  height: 10px;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 100%;
  transform: translate(-50%, -50%);
}

.banner h2 span {
  color: transparent;
  background-clip: text;
  background-image: linear-gradient(to top, #62DB54, #09ACF5);
}

.banner p {
  font-size: 18px;
  line-height: 26px;
}

@media (max-width: 767px) {
  .banner p {
    font-size: 14px;
    line-height: 20px;
  }
}

.banner p::after {
  content: "";
  width: 10px;
  height: 10px;
  background-color: #fff;
  position: absolute;
  top: 100%;
  left: 0;
  transform: translate(-50%, -50%);
}

.banner p::before {
  content: "";
  width: 10px;
  height: 10px;
  background-color: #fff;
  position: absolute;
  top: 100%;
  left: 100%;
  transform: translate(-50%, -50%);
}

.title {
  position: relative;
  font-size: 1.75rem;
  line-height: 2.5rem;
  font-weight: bold;
  color: #fff;
