}

@media (max-width: 767px) {
  .title {
    font-size: 1.5rem;
    line-height: 35px;
    font-weight: bold;
  }
}

.title::before {
  content: "";
  width: 50px;
  height: 2px;
  background-color: #62DB54;
  bottom: -16px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

