  .introduce-card {
    background-color: #202020;
    color: #fff !important;
  }
  .introduce-card span {
    color: transparent;
    background-clip: text;
    background-image: linear-gradient(to top, #62DB54, #09ACF5);
  }
}

.introduce-card span {
  font-size: 50px;
  line-height: 50px;
}

.class-cardA::after {
  content: "01";
}

.class-cardB::after {
  content: "02";
}

.class-cardC::after {
  content: "03";
}

.class-cardD::after {
  content: "04";
}

.class-card {
  position: relative;
  border-radius: 12px;
}

.class-card::after {
  font-size: 3rem;
  line-height: 3.5rem;
  font-weight: bold;
  color: #62DB54;
  background-color: #202020;
  padding: 0 8px;
