  width: max-content !important;
}

.course-card {
  border-radius: 24px;
  transition: all .3s ease-in;
  outline: 2px solid transparent;
}

.course-card:hover {
  outline: 2px solid #62DB54;
}

.course-card p {
  font-size: 1rem;
