}

.course-price::after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #fff;
  position: absolute;
  bottom: 70px;
}

.course-btn {
  color: #fff;
  background-image: linear-gradient(to left, #62DB54, #09ACF5);
