.learn p {
  font-size: 18px;
  line-height: 26px;
}

@media (max-width: 767px) {
  .learn p {
    color: #fff;
  }
}

@media (max-width: 767px) {
  .learn span {
    color: #62DB54;
  }
}

.learn .title:before {
  background-color: #fff;
